import {
    Component,
    h,
    Host,
    Prop,
    Listen,
    Event,
    EventEmitter,
} from '@stencil/core';
import { GridApi } from 'ag-grid-community';

/**
 * @category Data Tables
 * @description A container component for table actions, including search, export, and column management. Provides a unified interface for managing table interactions and data export.
 *
 * @slot column-manager - Used to place an atui-column-manager
 * @slot search - Used to place an atui-search
 * @slot dropdown-filters - Used to place an atui-table-filters
 * @slot export-menu - Used to place an atui-export-menu
 * @slot actions - Used to place actions related to table content

*/
@Component({
    tag: 'atui-table-actions',
    shadow: false,
})
export class AtuiTableActionsComponent {
    /**
     * AG Grid Api (provided by atui-table createGrid method)
     */
    @Prop()
    ag_grid: GridApi;

    /**
     * Emits an event when 'Export CSV' is pressed on provided atui-table-export-menu
     */
    @Event() atuiExportCsv: EventEmitter;

    /**
     * Emits an event when 'Export PDF' is pressed on provided atui-table-export-menu
     */
    @Event() atuiExportPdf: EventEmitter;

    /**
     * Emits an event when filters change
     */
    @Event() atuiChange: EventEmitter<any>;

    @Listen('atuiChange')
    changeHandler(event: CustomEvent) {
        const target = event.target as HTMLElement;
        switch (target.slot) {
            case 'column-manager':
                this.ag_grid.setColumnsVisible(
                    [event.detail.id],
                    event.detail.checked,
                );
                break;
            case 'search':
                this.ag_grid.setGridOption('quickFilterText', event.detail);
                break;
            case 'export-menu':
                if (event.detail === 'CSV') this.atuiExportCsv.emit();
                if (event.detail === 'PDF')
                    this.atuiExportPdf.emit(this.getVisibleColumns());
                break;
            case 'dropdown-filters':
                this.atuiChange.emit(event.detail);
                break;
        }
    }

    getVisibleColumns(): any[] {
        return this.ag_grid.getAllDisplayedColumns().map((column) => {
            const userProvidedColDef = column.getUserProvidedColDef();
            return {
                actualWidth: column.getActualWidth(),
                field: userProvidedColDef?.field || '',
                displayName: this.ag_grid.getDisplayNameForColumn(
                    column,
                    'header',
                ),
            };
        });
    }

    render() {
        return (
            <Host class="relative flex flex-col gap-8 pb-8 pt-8">
                <div class="flex justify-between">
                    <div class="flex">
                        <slot name="search" />
                    </div>
                    <div class="flex">
                        <slot name="export-menu" />
                        <slot name="column-manager" />
                        <slot name="actions" />
                    </div>
                </div>
                <slot name="filters" />
            </Host>
        );
    }
}
